import React from 'react'
import { SEO } from '../../components/SEO'
import Footer from '../../components/Footer'
import Header from '../../components/Header'
import Layout from '../../components/layout/Layout'
import { StaticImage } from 'gatsby-plugin-image'

const projectTwo = () => {
  return (
    <Layout>
      <Header />
      <section class='mx-auto max-w-4xl dark:text-slate-400'>
        <div class='prose md:prose-lg max-w-none container mx-auto flex flex-col items-center justify-center px-5 py-8'>
          <div class='w-full text-left lg:w-5/6'>
            <h1 class='mb-6 text-3xl font-semibold leading-tight text-gray-900 dark:text-slate-400 sm:mb-10 sm:text-4xl'>
              Complex Product Training | eLearning
            </h1>
          </div>
          <figure class='container mx-auto mb-6 flex w-5/6 flex-col items-center'>
            <StaticImage
              class='mb-1 rounded object-cover object-center'
              alt='Complex Product Training | eLearning'
              src='../../images/projects/product-training-orientation/Lead_Project_Image.jpg'
            />
            {/* <figcaption class='text-xs sm:text-sm'>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum
              facilis perspiciatis, error nihil laborum necessitatibus tota.
            </figcaption> */}
          </figure>
          <div class='flex justify-center mb-6'>
            <button class='inline-flex rounded border-0 bg-orange-200 py-2 px-6 text-lg font-medium text-slate-700 hover:bg-orange-300 focus:outline-none shadow-md'>
              <a
                href='https://product-training-and-orientation.netlify.app/'
                target={'_blank'}
              >
                Explore Project
              </a>
            </button>
          </div>
          <div class='w-full text-left lg:w-5/6'>
            {/* <h2 class='title-font mb-4 text-3xl font-medium text-gray-900 sm:text-4xl'>
              Microdosing synth tattooed vexillologist
            </h2> */}
            <p class='mb-8 leading-relaxed'>
              This eLearning-based complex product training helps a financial
              services organization orient its internal and external agents to
              understand a financial product in-depth.
            </p>
            <p class='prose mb-8 leading-relaxed dark:text-slate-400'>
              <ul className='list-disc list-outside'>
                <li>
                  <span className='font-bold'>Role:</span> Instructional Design,
                  Learning Experience Design
                </li>
                <li>
                  <span className='font-bold'>Target Audience:</span> Financial
                  Services Agents
                </li>
                <li>
                  <span className='font-bold'>Tools Used:</span> Articulate
                  Storyline, Adobe Illustrator, Microsoft PowerPoint
                </li>
                <li>
                  <span className='font-bold'>Client:</span> A fictional
                  financial services organization (name not disclosed to respect
                  the client's non-disclosure agreement)
                </li>
              </ul>
            </p>

            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Title Screen: Home Improvement Loan Product Orientation'
                src='../../images/projects/product-training-orientation/02-Title Screen.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Title Screen: Home Improvement Loan Product Orientation
              </figcaption>
            </figure>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Overview
            </h2>
            <p class='mb-8 leading-relaxed'>
              A medium-sized financial services organization had plans to launch
              a new product - a home improvement loan. However, a key step
              before the launch was to ensure that all the in-house and external
              agents were abreast with the new product.
            </p>
            <p class='mb-8 leading-relaxed'>
              The currently available training material was found to less
              effective as almost 40% of agents were unable to clear the post
              product training assessment. The Sales Manager contacted me to
              analyze the training material. The manager, who was also the
              Subject Matter Expert (SME), and I agreed that there were three
              key pitfalls in the training material.
            </p>
            <p class='mb-8 leading-relaxed'>
              One: the material was overloaded with unnecessary details that the
              agents on the ground would never need. Two: the training was
              filled with key financial information but was devoid of context or
              real-world scenarios for learners. Three: the training only
              focused on 'knowing' but not much on the 'doing' part of learning.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Product Training Navigation Menu'
                src='../../images/projects/product-training-orientation/03-Navigation Menu.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Product Training Navigation Menu
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              To address the above, I proposed an eLearning-based product
              training with a locked linear navigation where the next module
              only unlocks after the previous one has been completed. This
              progressive style of learning ensures that the learners would
              first uncover basic information related to the financial loan
              product, before they explore the key processes to apply for the
              loan. Once they are through the first two modules, they would then
              be exposed to common customer scenarios, where they get to make
              key decisions related to serving the loan applicants.
            </p>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Process
            </h2>
            <p class='mb-8 leading-relaxed'>
              I worked closely with the SME to ideate on, design and develop
              this interactive eLearning product orientation. The following were
              the key stages:
            </p>
            <p class='prose mb-8 leading-relaxed dark:text-slate-400'>
              <ul className='list-decimal list-outside'>
                <li>Categorizing content into progressive modules</li>
                <li>Rapid prototyping</li>
                <li>Developing the eLearning package in Storyline</li>
              </ul>
            </p>
            <h3 class='title-font mb-4 text-xl font-medium text-gray-700 dark:text-slate-400 sm:text-2xl'>
              Categorizing Content into Progressive Modules
            </h3>
            <p class='mb-8 leading-relaxed'>
              From the very beginning of the project, it was imperative to chunk
              this long-form technical content into modules. The original
              training documents combined with key insights from the SME served
              as input for this exercise. After long deliberations and
              discussions, it was decided that the content should move the
              learner progressively from 'learning' about the home improvement
              loan to 'doing' - in other words, making key decisions pertaining
              to the customers' queries about this product.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Locked Navigation Menu Ensures Progressive Learning'
                src='../../images/projects/product-training-orientation/04-Locked Navigation.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Locked Navigation Menu Ensures Progressive Learning
              </figcaption>
            </figure>
            <h3 class='title-font mb-4 text-xl font-medium text-gray-700 dark:text-slate-400 sm:text-2xl'>
              Rapid Prototyping
            </h3>
            <p class='mb-8 leading-relaxed'>
              Once the higher-level content flow was approved, it was agreed
              that we should initiate rapid prototyping for each module. Most
              importantly, I had to make sure that the client gets clarity on
              the functionality and interactivity available to the learners.
            </p>
            <p class='mb-8 leading-relaxed'>
              This allowed us the flexibility to make changes, if any, before a
              full-blown package was developed in Articulate Storyline with all
              the bells and whistles.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Rapid Prototyping of Most Common Layouts'
                src='../../images/projects/product-training-orientation/05-Rapid Prototyping of Common Slide Layouts.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Rapid Prototyping of Most Common Layouts
              </figcaption>
            </figure>
            <h3 class='title-font mb-4 text-xl font-medium text-gray-700 dark:text-slate-400 sm:text-2xl'>
              Developing the eLearning Package in Storyline
            </h3>
            <p class='mb-8 leading-relaxed'>
              Once the storyboard was approved, the content was in place. This
              was the perfect time to draw inspiration from client's existing
              branding, such as their website, brochures and online
              advertisements to decide the color scheme and typography.
            </p>
            <p class='mb-8 leading-relaxed'>
              This ensured consistency of branding as the design/development
              continued in Articulate Storyline. Another aspect which required
              much thought and planning was the programming of the locked
              navigation menu. We had to make sure that the future sections were
              locked until the current section was explored completely.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Click and Reveal Interactivity to Explore Main Features'
                src='../../images/projects/product-training-orientation/07-Click and Reveal.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Click and Reveal Interactivity to Explore Product's Main
                Features
              </figcaption>
            </figure>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Content Accordion to Understand the Loan Application Process'
                src='../../images/projects/product-training-orientation/08-Content Accordion.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Content Accordion to Understand the Loan Application Process
              </figcaption>
            </figure>
            <p class='mb-8 leading-relaxed'>
              A range of interactive features also were added to make this
              concept more engaging and less monotonous. These include
              click-to-reveal, flashcards, split-screen comparison, content
              accordions, and scored quizzes with pop-out summary screens.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Scenarios Allowing Common Customer Queries to be Answered'
                src='../../images/projects/product-training-orientation/09-Scenarios Reflecting Common Customer Queries.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Scenarios Allowing Common Customer Queries to be Answered
              </figcaption>
            </figure>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Quiz Results Summarizing Each Customer Query and Ideal Response'
                src='../../images/projects/product-training-orientation/10-Summary and Feedback of Customer Queries.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Quiz Results Summarizing Each Customer's Queries and Ideal
                Response
              </figcaption>
            </figure>
            <h2 class='title-font mb-4 text-2xl font-medium text-gray-900 dark:text-slate-400 sm:text-3xl'>
              Results and Takeaways
            </h2>
            <p class='mb-8 leading-relaxed'>
              As sound instructional design principles were followed to organize
              the content and develop an eLearning concept, the client saw a
              rapid jump in agent assessment scores. This gave the much-need
              push required to launch the product feeling confident the internal
              agents and external representatives were ready to sell the home
              improvement loans and answer any query the prospective customers
              may have.
            </p>
            <figure class='container mx-auto mb-10 flex w-5/6 flex-col items-center'>
              <StaticImage
                class='mb-1 rounded object-cover object-center'
                alt='Split Screen Showing Next Steps at the End of the Product Orientation'
                src='../../images/projects/product-training-orientation/11-Next Steps.jpg'
              />
              <figcaption class='text-xs sm:text-sm'>
                Split Screen Showing Next Steps at the End of the Product
                Orientation
              </figcaption>
            </figure>
          </div>
        </div>
      </section>
      <Footer />
    </Layout>
  )
}

export default projectTwo

export const Head = () => (
  <SEO title='Ankeet Patel - Complex Product Training and Orientation | eLearning' />
)
